import './MainBlockHeaderItemSettings.css';
import icon_settings_white from '../../../../foto/settings_white.png';
import icon_settings_black from '../../../../foto/settings_black.png';
import {useDispatch} from "react-redux";
import {setShowSettings} from "../../../../redux/reducers/setting";
import {NavLink} from "react-router-dom";

export const MainBlockHeaderItemSettings = ({setIsShow}) => {

  // const dispatch = useDispatch();
  // const onsetShowSettings = () => dispatch(setShowSettings(true));

  return (
    <button
      className='Main-Block-Header-Item-Settings'
      onClick={() => setIsShow(true)}
    >
      <img
        src={icon_settings_white}
        alt='settings'
      />
    </button>
  );
};