import './SettingsPage.css';
import {useDispatch} from "react-redux";
import {ThunkSetLogout} from "../../redux/thunks/Auth";

export const SettingsPage = ({isShow, setIsShow}) => {
  const dispatch = useDispatch();

  const onLogOut = () => {
    dispatch(ThunkSetLogout());
  };

  return (
    <div
      className={`Settings-Page border-standard ${isShow ? 'SettingsPageOn': 'Settings-Page'}`}
    >
      <p> </p>
      <p> </p>
      <button onClick={() => setIsShow(false)} ></button>
      <button onClick={() => onLogOut()}></button>
    </div>
  );
};